class KeyListener {
    constructor(code) {
        this.isDown = false;
        this.isUp = true;
        this.upHandler = function (event) {
            if (event.keyCode === this.code) {
                if (this.isDown && this.release)
                    this.release();
                this.isDown = false;
                this.isUp = true;
                event.preventDefault();
            }
        };
        this.code = code;
    }
    downHandler(event) {
        if (event.keyCode === this.code) {
            if (this.isUp && this.press)
                this.press();
            this.isDown = true;
            this.isUp = false;
            event.preventDefault();
        }
    }
    ;
    static create(keyCode, down = null, up = null) {
        let keyListener = new KeyListener(keyCode);
        window.addEventListener("keydown", keyListener.downHandler.bind(keyListener), false);
        window.addEventListener("keyup", keyListener.upHandler.bind(keyListener), false);
        keyListener.press = down;
        keyListener.release = up;
        return keyListener;
    }
}
